import { NextRequest, NextResponse } from 'next/server';
import dbConnect from '../../../lib/db';
import { verifyIdToken } from '../../../lib/auth';
import Group from '../../../models/Group';
import GroupMember from '../../../models/GroupMember';
import User from '../../../models/User';

export async function POST(req: NextRequest) {
  try {
    await dbConnect();
    
    // Auth Check
    const authHeader = req.headers.get('Authorization');
    if (!authHeader?.startsWith('Bearer ')) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }
    const token = authHeader.split('Bearer ')[1];
    const user = await verifyIdToken(token);
    
    if (!user) {
      return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
    }

    const { name, currency, members } = await req.json();

    if (!name) {
       return NextResponse.json({ error: 'Group name is required' }, { status: 400 });
    }

    // 1. Create Group
    const newGroup = await Group.create({
      name,
      currency: currency || 'USD',
      createdBy: user.uid,
      icon: null // handled by upload separately if needed
    });

    // 2. Add Creator as Admin
    await GroupMember.create({
      groupId: newGroup._id,
      userId: user.uid,
      role: 'admin'
    });

    // 3. Add Invited Members (if they exist in DB)
    if (members && Array.isArray(members) && members.length > 0) {
       // Find users by email
       // Note: 'members' is array of emails strings
       const foundUsers = await User.find({ email: { $in: members } });
       
       const memberPromises = foundUsers.map((u) => {
          // Prevent duplicates if creator added themselves in list
          if (u.firebaseUid === user.uid) return null;

          return GroupMember.create({
             groupId: newGroup._id,
             userId: u.firebaseUid, // Using firebaseUid as the key linkage
             role: 'member'
          });
       });

       await Promise.all(memberPromises.filter(p => p !== null));
    }

    return NextResponse.json({ 
       success: true, 
       group: newGroup 
    }, { status: 201 });

  } catch (error: any) {
    console.error("Create Group Error:", error);
    return NextResponse.json({ error: error.message || 'Internal Server Error' }, { status: 500 });
  }
}
